import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import('@/components/layout/Layout'),
    children: [
      {
        path: '',
        component: () => import("@/views/home/Home"),
      },
      {
        path: 'posts',
        component: () => import("@/views/posts"),
        redirect: 'posts/38447162',
        children: [
          {
            path: '38447162',
            component: () => import("@/views/posts/38447162")
          },
          {
            path: '38466050',
            component: () => import("@/views/posts/38466050")
          },
          {
            path: '38498128',
            component: () => import("@/views/posts/38498128")
          },
          {
            path: '38725142',
            component: () => import("@/views/posts/38725142")
          },
          {
            path: 'other',
            component: () => import("@/views/posts/Other")
          },
        ]
      },
      {
        path: 'letter',
        component: () => import("@/views/letter/Letter"),
      },
      {
        path: 'chat',
        component: () => import("@/views/chat/Chat"),
      },
      {
        path: 'recreation',
        component: () => import("@/views/recreation/Recreation"),
      },
      {
        path: 'events',
        component: () => import("@/views/events/Events"),
      },
      {
        path: 'resources',
        component: () => import("@/views/resources/Resources"),
      },
      {
        path: 'about',
        component: () => import("@/views/about/About"),
      },
    ]
  },
  {
    path: '*',
    redirect: '/',
  }
]

const router = new VueRouter({
  // mode: 'history',
  mode: 'hash',
  routes
})

export default router
